import { useSelector } from "react-redux";
import { useSearchParams } from "react-router-dom";
import InstituteNavbarConfig from "../Config/InstituteNavbarConfig";
import InstituteMemberConfig from "../Config/InstituteMemberConfig";
import GoBack from "../../Shared/StaffStudentProfile/GoBack";
import { Suspense, useEffect } from "react";
import QvipleLoader from "../../Loader/QvipleLoader";
import StudentFormSetting from "./FormSetting/StudentFormSetting";
import { useOneInstituteDashboard } from "../../../hooks/dashboard/dashboard-api";

const InstituteStudent = () => {
  const [searchParams] = useSearchParams();
  const instituteId = useSelector((state) => state.idChange.id);
  const { oneInstituteDashboard, oneInstituteDashboardRefetch } =
    useOneInstituteDashboard({
      id: instituteId,
      skip: !instituteId,
    });
  useEffect(() => {
    if (instituteId) {
      oneInstituteDashboardRefetch();
    }
  }, [oneInstituteDashboardRefetch]);

  return (
    <InstituteNavbarConfig>
      <InstituteMemberConfig>
        <section
          style={{
            width: "100%",
          }}
        >
          <GoBack
            customStyle={{
              width: "100%",
              marginTop: "0",
              marginBottom: "1rem",
            }}
          />
          <Suspense fallback={<QvipleLoader />}>
            {searchParams.get("type") === "form" ? (
              <StudentFormSetting
                instituteId={instituteId}
                formId={oneInstituteDashboard?.institute?.student_form_setting}
              />
            ) : null}
          </Suspense>
        </section>
      </InstituteMemberConfig>
    </InstituteNavbarConfig>
  );
};

export default InstituteStudent;
