import { useTranslation } from "react-i18next";
import ModalMenu from "../../../../Ui/Modal/ModalMenu";
import { useState } from "react";
import EditAddField from "./EditAddField";

const StudentFormSettingCardMenu = ({ onClose, onRefetch, data }) => {
  const { t } = useTranslation();
  const [disabled, setDisabled] = useState(false);
  const [step, setStep] = useState("");

  const onEditStep = () => {
    setStep(1);
  };
  return (
    <>
      {step === 1 ? (
        <EditAddField
          onClose={() => setStep("")}
          data={data}
          onParentClose={onClose}
          onRefetch={onRefetch}
        />
      ) : (
        <ModalMenu onClose={onClose} disabled={disabled}>
          <h6 onClick={onEditStep}>{t("edit")}</h6>
          <h6>{t("delete")}</h6>
        </ModalMenu>
      )}
    </>
  );
};

export default StudentFormSettingCardMenu;
