import { useEffect, useState } from "react";
import { useTranslation } from "react-i18next";
import {
  existingRequiredField,
  requiredField,
} from "../../../../Validation/functions/checkingRequiredField";
import Modal from "../../../../Ui/Modal/Modal";
import UserMemberContentWrapper from "../../../../Ui/UserMember/UserMemberContentWrapper";
import BorderBottom from "../../../../Ui/Border/BorderBottom";
import style from "../../../../UserStaffMember/FinanceManager/Income/Income.module.css";
import { assestsUserFinanceUrl } from "../../../../Services/UrlConfig/AssestsBaseUrl";
import { useInstituteStudentUpdateSectionChecklist } from "../../../../Hooks/ApiHooks/InstituteStudent/institute-student-api";
import QvipleLoader from "../../../../Loader/QvipleLoader";
import CreateButton from "../../../../Ui/Button/CreateButton";
import FormInput from "../../../../Ui/Input/FormInput";
import TileWrapper from "../../../../Ui/Tab/Tile/TileWrapper";
import TileCard from "../../../../Ui/Tab/Tile/TileCard";
import {
  fTypes,
  optionList,
} from "../../../../Constant/FormSetting/form_setting_constant";
const EditAddField = ({ onClose, formId, onRefetch, sectionId, data }) => {
  const { t } = useTranslation();
  const [errorField, setErrorField] = useState("");
  const [disabled, setDisabled] = useState(false);

  const [state, setState] = useState({
    form_checklist_name: "",
    form_checklist_key: "",
    form_checklist_visibility: true,
    form_checklist_placeholder: "",
    form_checklist_lable: "",
    form_checklist_typo: "",
    form_checklist_typo_option_pl: [],
  });

  const [instituteStudentUpdateSectionChecklist] =
    useInstituteStudentUpdateSectionChecklist();

  useEffect(() => {
    if (data) {
      let dt = [];

      for (let d of data?.form_checklist_typo_option_pl) {
        dt.push({
          value: d,
        });
      }
      setState({
        form_checklist_name: data?.form_checklist_name ?? "",
        form_checklist_key: data?.form_checklist_key ?? "",
        form_checklist_visibility: data?.form_checklist_visibility ?? false,
        form_checklist_placeholder: data?.form_checklist_placeholder ?? "",
        form_checklist_lable: data?.form_checklist_lable ?? "",
        form_checklist_typo: data?.form_checklist_typo ?? "",
        form_checklist_typo_option_pl: dt,
      });
    }
  }, [data]);

  const onChange = (e) => {
    setState((prev) => ({
      ...prev,
      [e.target.name]: e.target.value,
    }));
  };
  const onTile = (value) => {
    if (optionList?.includes(value)) {
      setState((prev) => ({
        ...prev,
        form_checklist_typo: value,
        form_checklist_typo_option_pl: [
          ...prev?.form_checklist_typo_option_pl,
          { value: "" },
        ],
      }));
    } else {
      setState((prev) => ({
        ...prev,
        form_checklist_typo: value,
        form_checklist_typo_option_pl: [],
      }));
    }
  };

  const onAddOption = () => {
    setState((prev) => ({
      ...prev,
      form_checklist_typo_option_pl: [
        ...prev.form_checklist_typo_option_pl,
        { value: "" },
      ],
    }));
  };
  const onRemoveOption = (ind) => {
    let mp = [...state.form_checklist_typo_option_pl];
    let dp = [];
    for (let i = 0; i < mp?.length; mp++) {
      if (i === ind) {
      } else {
        dp.push(mp[i]);
      }
    }
    setState((prev) => ({
      ...prev,
      form_checklist_typo_option_pl: dp,
    }));
  };

  const onOptionChange = (e, optIndex) => {
    const inputarr = [...state.form_checklist_typo_option_pl];
    inputarr[optIndex].value = e.target.value;
    setState((prev) => ({ ...prev, form_checklist_typo_option_pl: inputarr }));
  };

  const onAdd = () => {
    let validation = requiredField(state, ["form_checklist_typo_option_pl"]);
    if (existingRequiredField(validation) && formId && sectionId) {
      setDisabled((pre) => !pre);
      let choose_opt = [];

      for (let s of state.form_checklist_typo_option_pl) {
        if (s?.value) {
          choose_opt.push(s?.value);
        }
      }

      instituteStudentUpdateSectionChecklist({
        formId: formId,
        bodyParameter: {
          fsid: sectionId,
          checklist: [
            {
              form_checklist_name: state?.form_checklist_lable ?? "",
              form_checklist_key: getUnderScoreFormat(
                state.form_checklist_lable
              ),
              form_checklist_visibility: state.form_checklist_visibility,
              form_checklist_placeholder:
                state?.form_checklist_placeholder ?? "",
              form_checklist_lable: state?.form_checklist_lable ?? "",
              form_checklist_typo: state?.form_checklist_typo ?? "",
              form_checklist_typo_option_pl: choose_opt,
            },
          ],
        },
      })
        .then(() => {
          onRefetch();
          setDisabled((pre) => !pre);
          onClose();
        })
        .catch({});
    } else {
      setErrorField(validation);
    }
  };
  return (
    <Modal onClose={onClose}>
      <div
        className={style.modal_container}
        style={{
          maxWidth: "45rem",
        }}
      >
        <UserMemberContentWrapper>
          <div className={style.modal_container_outer_header}>
            <div className={style.modal_container_header}>
              <h6>{t("update_add_field")}</h6>
            </div>
            <img
              src={`${assestsUserFinanceUrl}/close.svg`}
              alt="close icon"
              onClick={onClose}
            />
          </div>
        </UserMemberContentWrapper>
        <BorderBottom />
        <UserMemberContentWrapper
          customStyle={{
            paddingTop: "0",
          }}
        >
          <TileWrapper
            label="field_type"
            customTileContainer={{
              flexWrap: "wrap",
            }}
          >
            {fTypes?.map((tile) => (
              <TileCard
                key={tile?.key}
                tile={tile}
                onTile={onTile}
                activeTile={state?.form_checklist_typo}
              />
            ))}
          </TileWrapper>
          <FormInput
            label={t("field_label")}
            placeholder={t("field_label_placeholder")}
            name={"form_checklist_lable"}
            value={state.form_checklist_lable}
            type={"text"}
            onChange={onChange}
            errorShow={errorField["form_checklist_lable"]}
          />
          <FormInput
            label={t("field_placeholder")}
            placeholder={t("field_placeholder_placeholder")}
            name={"form_checklist_placeholder"}
            value={state.form_checklist_placeholder}
            type={"text"}
            onChange={onChange}
            errorShow={errorField["form_checklist_placeholder"]}
          />

          {optionList?.includes(state.form_checklist_typo) ? (
            <div
              style={{
                padding: "1rem",
              }}
            >
              <div className={style.modal_container_header}>
                <h6>{t("choices")}</h6>
              </div>

              {state?.form_checklist_typo_option_pl?.map((pl, id) => (
                <FormInput
                  label={t("field_label")}
                  placeholder={t("field_label_placeholder")}
                  name={`${pl}${id}`}
                  value={pl?.value}
                  type={"text"}
                  onChange={(e) => onOptionChange(e, id)}
                  key={`${pl}${id}`}
                  customFormInputLabel={{
                    display: "flex",
                    alignItmes: "center",
                    justifyContent: "space-between",
                  }}
                >
                  <CreateButton
                    label={t("remove")}
                    customStyle={{
                      margin: "0 0 0 0",
                      padding: "0.1rem 0.5rem",
                      width: "fit-content",
                      fontSize: "12px",
                      fontWeight: "400",
                    }}
                    onAction={() => onRemoveOption(id)}
                  />
                </FormInput>
              ))}
              <CreateButton
                label="add_option"
                customStyle={{
                  paddingBlock: "0.2rem",
                  margin: "0",
                  marginTop: "0.7rem",
                  width: "fit-content",
                }}
                onAction={onAddOption}
              />
            </div>
          ) : null}

          <CreateButton label="update" onAction={onAdd} />
        </UserMemberContentWrapper>
        {disabled && <QvipleLoader />}
      </div>
    </Modal>
  );
};

export default EditAddField;

const getUnderScoreFormat = (val) => {
  let st = val?.replaceAll(" ", "_");
  return st?.toLowerCase();
};
