import { useEffect, useState } from "react";
import { useTranslation } from "react-i18next";
import {
  existingRequiredField,
  requiredField,
} from "../../../../Validation/functions/checkingRequiredField";
import Modal from "../../../../Ui/Modal/Modal";
import UserMemberContentWrapper from "../../../../Ui/UserMember/UserMemberContentWrapper";
import BorderBottom from "../../../../Ui/Border/BorderBottom";
import style from "../../../../UserStaffMember/FinanceManager/Income/Income.module.css";
import { assestsUserFinanceUrl } from "../../../../Services/UrlConfig/AssestsBaseUrl";
import { useInstituteStudentUpdateSectionChecklist } from "../../../../Hooks/ApiHooks/InstituteStudent/institute-student-api";
import QvipleLoader from "../../../../Loader/QvipleLoader";
import CreateButton from "../../../../Ui/Button/CreateButton";
import FormInput from "../../../../Ui/Input/FormInput";
const EditFormSection = ({ onClose, formId, onRefetch, sectionId, data }) => {
  const { t } = useTranslation();
  const [errorField, setErrorField] = useState("");
  const [disabled, setDisabled] = useState(false);

  const [state, setState] = useState({
    section_name: "",
    section_key: "",
    section_visibilty: true,
  });

  useEffect(() => {
    if (data) {
      setState({
        section_name: data?.section_name ?? "",
        section_key: data?.section_key ?? "",
        section_visibilty: data?.section_visibilty ?? true,
      });
    }
  }, [data]);

  const [instituteStudentUpdateSectionChecklist] =
    useInstituteStudentUpdateSectionChecklist();
  const onChange = (e) => {
    setState((prev) => ({
      ...prev,
      [e.target.name]: e.target.value,
    }));
  };

  const onAdd = () => {
    let validation = requiredField(state, []);
    if (existingRequiredField(validation) && formId && sectionId) {
      setDisabled((pre) => !pre);
      instituteStudentUpdateSectionChecklist({
        formId: formId,
        bodyParameter: {
          section_name: state.section_name,
          section_key: getUnderScoreFormat(state.section_name),
          section_visibilty: true,
          fsid: sectionId,
          checklist: [],
          cid: "",
        },
      })
        .then(() => {
          onRefetch();
          setDisabled((pre) => !pre);
          onClose();
        })
        .catch({});
    } else {
      setErrorField(validation);
    }
  };

  return (
    <Modal onClose={onClose}>
      <div className={style.modal_container}>
        <UserMemberContentWrapper>
          <div className={style.modal_container_outer_header}>
            <div className={style.modal_container_header}>
              <h6>{t("update_add_section")}</h6>
            </div>
            <img
              src={`${assestsUserFinanceUrl}/close.svg`}
              alt="close icon"
              onClick={onClose}
            />
          </div>
        </UserMemberContentWrapper>
        <BorderBottom />
        <UserMemberContentWrapper
          customStyle={{
            paddingTop: "0",
          }}
        >
          <FormInput
            label={t("section_name")}
            placeholder={t("section_name_placeholder")}
            name={"section_name"}
            value={state.section_name}
            type={"text"}
            onChange={onChange}
            errorShow={errorField["section_name"]}
          />

          <CreateButton label="update" onAction={onAdd} />
        </UserMemberContentWrapper>
        {disabled && <QvipleLoader />}
      </div>
    </Modal>
  );
};

export default EditFormSection;

const getUnderScoreFormat = (val) => {
  let st = val?.replaceAll(" ", "_");
  return st?.toLowerCase();
};
